.header {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
}

.header__nav {
  padding: 35px 103px;
  max-width: 1366px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: start;
  gap: 60px;

  @media (max-width: $vp-1024) {
    gap: 40px;
    padding: 30px 45px;
  }

  @media (max-width: $vp-768) {
    padding: 20px 15px;
  }
}

.header--position {
  @media (max-width: $vp-768) {
    position: relative;
  }
}
